import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import login from '@/components/login/login.vue'
import index from '@/components/index/index.vue'
import navigation from '@/components/navigation/navigation.vue'
import bookmark from '@/components/bookmark/bookmark.vue'
import bookmark_searchView from '@/components/bookmark/bookmark_searchView.vue'
import routerPathArray from '@/router/routerPathArray.js'



const router = new VueRouter({
  routes: [
    // 重定向的路由规则
    {
      path: '/',
      // redirect: '/index/navigation'
      redirect: '/login'
    },
    {
      name:"login",
      path: '/login',
      component: login
    },
    
    // 路由规则
    {
      name:"index",
      path: '/index/navigation',
      component: index,
      children: [,
        {
          name:"navigation",
          path: '/index/navigation',
          component: navigation
        },
        {
          name: "bookmark",
          path: '/index/bookmark',
          component: bookmark
        },
        // 如果需要用params传参数，需要name属性值
        {
          name: "bookmark_searchView",
          path: '/index/bookmark_searchView',
          component: bookmark_searchView
        },

      ]
    },

  ]
})

router.beforeEach(function (to, from, next) {
  // if (routerPathArray.indexOf(to.path) !== -1) {
  //   const token = localStorage.getItem('token')
  //   if (token) {
  //     next()
  //   } else {
  //     next('/login')
  //   }
  // } else {
  //   next()
  // }
  next()
})

export default router
