<template>
  <div class="nav_page">
    <div class="nav_page_header">
      <div class="nav_page_header_left">1</div>
      <div class="nav_page_header_right">{{ clockData }}</div>
    </div>
    <div class="nav_page_content">
      <van-row  >
        <van-col span="4" align="right">
          <van-image
            width="40"
            height="40"
            src="@/assets/icon/navigation/seach/bd_logo.png"
          />
        </van-col>
        <van-col span="16">
          <input class="nav_page_content_input" type="text" name="" />
        </van-col>
        <van-col span="4" align="left"><van-button type="info">百度</van-button> </van-col>
      </van-row>
    </div>
    <div ref="pNode">测试文字</div>
    <button @click="testButton">测试按钮</button>
  </div>
</template>

<script>
import Vue from "vue";
import Vant from "vant";
import "vant/lib/index.less";

Vue.use(Vant);

export default {
  data() {
    return {
      clockData: "",
    };
  },
  methods: {
    testButton(){
      console.log(this.$refs.pNode)
      this.$refs.pNode.style.color="#ccc"



    },
    // 时钟
    setDataTime() {
      // 月
      var mymonth = new Date().getMonth() + 1;
      // 日
      var myday = new Date().getDate();
      // 星期
      var weekdayList = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      var myweekday = new Date().getDay();
      var weekday = weekdayList[myweekday];
      // 时 分
      var time = new Date();
      var hour = "00" + time.getHours();
      var minute = "00" + time.getMinutes();
      var attime =
        hour.substring(hour.length - 2, hour.length) +
        ":" +
        minute.substring(minute.length - 2, minute.length);

      var dataTime = mymonth + "月" + myday + "日 " + weekday + " " + attime;
      this.clockData = dataTime;
    },
  },
  created() {
    this.setDataTime();
    setInterval(this.setDataTime, 30000);
  },
};
</script>

<style lang="less" scoped>
.nav_page_header {
  white-space: nowrap;
