    height: 9vw;
    text-indent: 2vw;
    font-size: 4vw;
    width: 90vw;
    font-family: Arial, Helvetica, sans-serif;
    border: 1px solid #d8d8d8;
    border-radius: 1px;
    margin: 0;
  }
}
</style>