import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
export default new Vuex.Store({
  state: {
    // 全局数据
    pageName: "navigation",
    navigationPage_selectedTag: "",
    bookmarkPage_searchInputVal: "",
  },
  mutations: {
    // 第一个参数都是state
    changepageName(state, newPageName) {
      state.pageName = newPageName
    },
    changeNavigationPage_selectedTag(state, newSelectedTag) {
      state.navigationPage_selectedTag = newSelectedTag
    },
    changeBookmarkPage_searchInputVal(state, searchInputVal) {
      state.bookmarkPage_searchInputVal = searchInputVal
    },
  },
  actions: {
    // 可以通过actions间接的执行异步操作
    changeFormData_async(context, waitModifyData) {
      setTimeout(() => {
        context.commit("changeFormData2", waitModifyData)
      }, 1000);
    },
  },
  getters: {
    // 处理state的数据
    changeStateData(state) {
      return '被getters修改的数据：' + state.editPage_main_page_formData.bz
    }
  }
})
