<template>
  <div id="login_page">
    <div id="login_content">
      <div id="login_content_top">
        <el-radio-group
          id="login_content_top_radio"
          v-model="radio_index"
          @change="radio_change"
        >
          <el-radio-button label="navigation">导航</el-radio-button>
          <el-radio-button label="bookmark">书签</el-radio-button>
          <el-radio-button label="bookmark_searchView"
            >书签搜索</el-radio-button
          >
        </el-radio-group>
      </div>
      <div id="login_content_bottom">
        <el-input
          id="login_page_textarea"
          ref="login_page_textarea_node"
          placeholder="请输入"
          v-model="login_page_textarea"
          show-password
          v-on:input="textarea_change"
        ></el-input>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      login_page_textarea: "",
      radio_index: "navigation",
    };
  },
  methods: {
    textarea_change() {
      // console.log(this.login_page_textarea);
      let loginKeyArray = ["1115", "5201314"];
      if (loginKeyArray.indexOf(this.login_page_textarea) > -1) {
        this.$router.push({
          name: this.radio_index,
        });
      }
    },
    radio_change(radio_index) {
      console.log("radio_index:", radio_index);
      this.$store.commit("changepageName", radio_index);
    },
  },
  created() {},
  mounted() {
    this.$refs.login_page_textarea_node.focus();
  },
};
</script>

<style lang="less" scoped>
#login_page {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 100%;
  flex-wrap: wrap;

  #login_content {
    position: relative;
    display: block;
    width: 250px;
    height: 100px;

    #login_content_top {
      position: relative;
      display: flex;
      justify-content: center;
      margin-bottom: 10px;
      width: 250px;
    }

    #login_content_bottom {
      position: relative;
      display: block;
      width: 100%;
    }
  }
}

#login_page_textarea {
}
</style>
