<template>
  <div id="index_page">
    <div id="index_header">
      <el-menu
        id="index_header_menu"
        :default-active="navMenu_index"
        class="el-menu-demo"
        mode="horizontal"
        @select="navMenu_change"
      >
        <el-menu-item index="navigation">导航</el-menu-item>
        <el-menu-item index="bookmark">书签</el-menu-item>
        <el-menu-item index="bookmark_searchView">书签搜索</el-menu-item>
      </el-menu>
    </div>
    <div id="index_content">
      <router-view @navMenu_index_Change="navMenu_index_Change"></router-view>
    </div>
    <div id="index_bottom"></div>
  </div>
</template>

<script>
import navigation from "@/components/navigation/navigation.vue";
import bookmark from "@/components/bookmark/bookmark.vue";
import bookmark_searchView from "@/components/bookmark/bookmark_searchView.vue";

export default {
  name: "index_page",
  data() {
    return {
      navMenu_index: "navigation",
    };
  },
  watch: {
    // "$store.state.pageName": (newPageName) => {
    "$store.state.pageName"(newPageName) { 
      this.navMenu_index = newPageName;
    },
  },
  components: { navigation, bookmark, bookmark_searchView },
  methods: {
    navMenu_change(index) {
      console.log("底部标签切换", index);
      this.$router.push({
        name: index,
      });
      this.$store.commit("changepageName", index);
    },
    navMenu_index_Change(index) {
      console.log("index:", index);
      this.navMenu_index = index;
    },
  },
  created() {
    this.navMenu_index = this.$store.state.pageName;
  },
};
</script>
<style lang="less" scoped>
#index_page{
  background-color: #fff;
}
#index_header {
  position: fixed;
  top: 0;
  z-index: 10;
  width: 100%;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;

  #index_header_menu {
    border: none;
  }
}
#index_header:hover {
  opacity: 1;
  border-bottom: solid 1px #e6e6e6;
}
</style>
 