<template>
  <div id="bookmarks_page">
    <div id="bookmarks_header">
      <h2>个人书签-搜索</h2>
    </div>
    <div id="bookmarks_content">
      <div id="bookmarks_content_box">
        <div
          :name="item.id"
          class="bookmarks_content_box_item_item"
          v-for="(item, index) in listDataArray"
          :key="item.id"
          :node_name="item.node_name"
          :node_attrVal="item.node_attrVal"
          :title="item.node_content + '|' + item.node_attrVal"
          @click="jumpNewPage(item.node_attrVal)"
          v-color="getRandomColor()"
        >
          {{ index + 1 }}.{{ item.node_content }}
        </div>
      </div>
    </div>

    <div id="bookmarks_button">
      <div
        id="bookmarks_searchContent"
        v-show="bookmarks_searchContent_input_hide"
      >
        <el-input
          id="bookmarks_searchContent_input"
          v-model="bookmarks_searchContent_input_val"
          placeholder="请输入内容"
          @keyup.enter.native="goSearchViewPage"
          ref="bookmarks_searchContent_input"
        ></el-input>
        <el-button
          id="bookmarks_searchContent_button"
          type="primary"
          size="small"
          @click="goSearchViewPage"
          >搜索</el-button
        >
        <el-button type="info" size="small" plain @click="reset"
          >重置</el-button
        >
      </div>
      <div id="bookmarks_button_ctrl">
        <el-tooltip
          class="item"
          effect="dark"
          content="更多功能"
          placement="left"
        >
          <el-button
            id="bookmarks_button_ctrl_icon"
            type="primary"
            @click="bookmarks_button_list_hide = !bookmarks_button_list_hide"
            ><i class="el-icon-more"></i
          ></el-button>
        </el-tooltip>
      </div>
      <div id="bookmarks_button_list" v-show="bookmarks_button_list_hide">
        <el-tooltip class="item" effect="dark" content="折叠" placement="left">
          <el-button
            class="bookmarks_button_list_item"
            type="primary"
            @click="folder"
          >
            <i class="el-icon-folder"></i>
          </el-button>
        </el-tooltip>
        <el-tooltip class="item" effect="dark" content="展开" placement="left">
          <el-button
            class="bookmarks_button_list_item"
            type="primary"
            @click="open"
          >
            <i class="el-icon-folder-opened"></i>
          </el-button>
        </el-tooltip>

        <el-tooltip class="item" effect="dark" content="底部" placement="left">
          <el-button
            class="bookmarks_button_list_item"
            type="primary"
            @click="goBottom"
          >
            <i class="el-icon-arrow-down"></i>
          </el-button>
        </el-tooltip>
        <el-tooltip class="item" effect="dark" content="顶部" placement="left">
          <el-button
            class="bookmarks_button_list_item"
            type="primary"
            @click="goTop"
          >
            <i class="el-icon-arrow-up"></i>
          </el-button>
        </el-tooltip>
        <el-tooltip class="item" effect="dark" content="搜索" placement="left">
          <el-button
            class="bookmarks_button_list_item"
            type="primary"
            @click="searchDisplay"
          >
            <i class="el-icon-search"></i>
          </el-button>
        </el-tooltip>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import bookmark_data from "@/assets/data/bookmark.json";
// console.log(bookmark_data);

import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";

export default {
  data() {
    return {
      listDataArray: [],
      bookmarks_button_list_hide: false,
      bookmarks_searchContent_input_val: "",
      bookmarks_searchContent_input_hide: true,
    };
  },
  methods: {
    jumpNewPage(node_attrVal) {
      // console.log(node_attrVal);
      window.open(node_attrVal);
    },
    // 返回颜色数值
    randomRgbColorNum() {
      const num = Math.floor(Math.random() * 256);
      if (num <= 210) {
        return num;
      } else {
        this.randomRgbColorNum();
      }
    },
    // 获取随机颜色
    getRandomColor() {
      const r = this.randomRgbColorNum();
      const g = this.randomRgbColorNum();
      const b = this.randomRgbColorNum();
      return `rgb(${r},${g},${b})`;
    },
    // 搜索后过滤内容
    searchCreate(inputVal) {
      this.listDataArray = [];
      for (let i = 0; i < bookmark_data.length; i++) {
        try {
          let childrenContent_array = bookmark_data[i]["childrenContent"];
          for (let j = 0; j < childrenContent_array.length; j++) {
            let node_content = childrenContent_array[j]["node_content"];
            let node_attrVal = childrenContent_array[j]["node_attrVal"];
            // console.log(node_content);
            if (
              node_content.indexOf(inputVal) > -1 ||
              node_attrVal.indexOf(inputVal) > -1
            ) {
              childrenContent_array[j]["id"] = i + "_" + j;
              this.listDataArray.push(childrenContent_array[j]);
            }
          }
        } catch (error) {
          // console.log(error);
          // console.log(bookmark_data[i]);
        }
      }
      console.log(this.listDataArray);
    },
    // 跳转到顶部
    goTop() {
      document.getElementById("bookmarks_content").scrollTop = 0;
    },
    // 跳转到底部
    goBottom() {
      document.getElementById("bookmarks_content").scrollTop =
        document.getElementById("bookmarks_content_box").scrollHeight;
    },
    // 按键显示搜索框
    keyDown() {
      // 监听按键触发
      document.body.onkeydown = (e) => {
        // 兼容多浏览器写法
        var keyCode = e.keyCode || e.which || e.charCode;
        // 用来监听键盘上的alt，shift，ctrl，meta键
        var altKey = e.altKey;
        var shiftKey = e.shiftKey;
        var ctrlKey = e.ctrlKey;
        var metaKey = e.metaKey;
        // console.log("key code=", e.keyCode);
        if (ctrlKey && keyCode == 70) {
          this.searchDisplay();
          return false;
        }
      };
    },
    // 搜索
    searchDisplay() {
      this.bookmarks_searchContent_input_hide =
        !this.bookmarks_searchContent_input_hide;
      setTimeout(() => {
        this.$refs.bookmarks_searchContent_input.focus();
      }, 500);
    },
    // 折叠
    folder() {
      this.activeNames = [];
    },
    // 展开
    open() {
      let array = [];
      for (const i in this.listDataArray) {
        array.push(this.listDataArray[i].id);
      }
      // console.log(array);
      this.activeNames = array;
    },
    // 跳转到搜索详情
    goSearchViewPage() {
      let inputVal = this.bookmarks_searchContent_input_val;
      this.searchCreate(inputVal);
    },
    // 重置搜索内容
    reset() {
      this.bookmarks_searchContent_input_val = "";
    },
  },
  created() {
    let inputVal = this.$store.state.bookmarkPage_searchInputVal;
    this.bookmarks_searchContent_input_val = inputVal;
    this.searchCreate(inputVal);
    this.keyDown();
  },
  directives: {
    color(element, element_value) {
      // console.log(element_value.value);
      element.style.color = element_value.value;
    },
  },
};
</script>

<style lang="less" scoped>
#bookmarks_page {
  position: fixed;
  display: block;
  height: 100%;
  width: 100%;
  overflow-y: auto;
  left: 0;
  top: 0;
}

#bookmarks_header {
  position: relative;
  display: block;
  height: 50px;
  color: #66ccff;
  text-align: center;
}

#bookmarks_content {
  position: relative;

  display: flex;

  align-items: center;

  justify-content: center;

  #bookmarks_content_box {
    position: relative;

    display: inline-block;

    max-width: 1400px;

    min-width: 600px;

    width: 90%;

    top: 0;

    height: 100%;
  }

  .bookmarks_content_box_item_item {
    width: 300px;
    text-decoration: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    position: relative;
    display: inline-block;
    overflow: hidden;
    padding: 0 12px;
    font-size: 18px;
    cursor: pointer;
  }
}

#bookmarks_button {
  #bookmarks_searchContent {
    position: fixed;
    display: flex;
    left: 50%;
    bottom: 30px;
    font-size: 40px;
    width: 300px;
    background-color: #fff;
    padding: 10px;
    margin-left: -150px;
    box-shadow: 0px 5px 10px 3px #ccc;
    border-radius: 5px;

    .el-input {
      margin-right: 10px;
    }
  }

  #bookmarks_button_ctrl_icon {
    position: fixed;
    display: flex;
    right: 30px;
    bottom: 30px;
    font-size: 40px;
    width: 50px;
    height: 50px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
  }
  #bookmarks_button_list {
    position: fixed;
    display: flex;
    right: 30px;
    bottom: 100px;
    width: 50px;

    flex-wrap: wrap-reverse;

    align-items: center;

    justify-content: center;

    .bookmarks_button_list_item {
      position: relative;
      display: flex;
      font-size: 30px;
      width: 40px;
      height: 40px;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      margin-top: 10px;
      margin-left: 0;
    }
  }
}
</style>
