<template>
  <div id="navigation_page">
    <div id="navigation_header">
      <div
        id="weather"
        class="navigation_header_item"
        v-text="weatherData"
      ></div>
      <div id="clock" class="navigation_header_item" v-text="clockData"></div>
    </div>
    <div id="navigation_search">
      <div class="seach_content">
        <form
          id="baiduSeach"
          class="seach_from"
          onSubmit="return baiduWithHttps(this)"
          action="http://www.baidu.com/baidu"
          target="_blank"
          v-show="baiduSeach_displayStatus"
        >
          <div
            id="bd_logo"
            class="seach_logo"
            alt="切换搜索引擎"
            @click="switchSeach"
          >
            <i></i>
          </div>
          <input
            id="b_ipt"
            class="seach_input"
            type="text"
            onfocus=""
            name="word"
            placeholder=""
            v-model="seachInputVal"
          />
          <input id="b_btn" class="seach_button" type="submit" value="百度" />
        </form>
        <form
          id="googleSeach"
          class="seach_from"
          method="get"
          action="http://www.google.com/search"
          target="_blank"
          v-show="googleSeach_displayStatus"
        >
          <div
            id="google_logo"
            class="seach_logo"
            alt="切换搜索引擎"
            @click="switchSeach"
          >
            <i></i>
          </div>
          <input
            id="g_ipt"
            class="seach_input"
            type="text"
            name="q"
            value=""
            placeholder=""
            v-model="seachInputVal"
          />
          <input
            id="g_btn"
            class="seach_button"
            type="submit"
            name="btnG"
            value="Google"
          />
        </form>
      </div>
    </div>
    <div id="collect">
      <div id="collect_content">
        <div
          class="collect_item"
          v-for="item in collectContentData"
          :key="item.groupName"
        >
          <div class="nav-group">
            <div class="nav-group-name">
              <div class="nav-group-name-text">
                {{ item.groupName }}
              </div>
            </div>
            <div class="nav-group-content">
              <div
                mark=" "
                class="nav-group-unit"
                v-for="groupContent_item in item.groupContent"
                :key="groupContent_item.groupName"
              >
                <a
                  :title="groupContent_item.internetSiteName"
                  :href="groupContent_item.internetSite"
                  target="_blank"
                  ><div class="del-icon">
                    <span class="nav-icon-span hide"
                      >{{ groupContent_item.internetSiteName[0] }}
                    </span>
                    <!-- <i
                        class="nav-icon"
                        alt=""
                        :style="
                          'background-image:url(' +
                          [
                            groupContent_item.remoteIconUrl
                              ? groupContent_item.remoteIconUrl
                              : sever_url + groupContent_item.localIconUrl,
                          ] +
                          ')'
                        "
                      ></i> -->
                    <i
                      class="nav-icon"
                      alt=""
                      :style="getIconUrl(groupContent_item)"
                    ></i>
                  </div>
                  <div class="nav-text">
                    {{ groupContent_item.internetSiteName }}
                  </div></a
                >
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div id="bookmarks" class="bookmarks_haveContent">
      <div id="bookmarks_content">
        <div
          class="bookmarks_content_item"
          itemName="${bookmarkDataItem}"
          couldClick="yes"
          v-for="item in bookmarksContentData"
          :key="item"
        >
          <a :title="item" target="_blank" @click="goBookmarkPage(item)">{{
            item
          }}</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Vue from "vue";
// import Vant from "vant";
// Vue.use(Vant);
// import "vant/lib/index.less";
// import "element-ui/lib/theme-chalk/index.css";
import axios from "axios";

import navData from "@/assets/data/navData.json";

import bd_logo_url from "@/assets/icon/navigation/seach/bd_logo.png";

export default {
  data() {
    return {
      sever_url: "",
      clockData: "",
      weatherData: "",
      seachInputVal: "",
      baiduSeach_displayStatus: true,
      googleSeach_displayStatus: false,
      collectContentData: [],
      bookmarksContentData: [],
    };
  },
  methods: {
    testButton() {
      console.log(this.$refs.pNode);
      this.$refs.pNode.style.color = "#ccc";
    },
    // 时钟
    setDataTime() {
      // 月
      var mymonth = new Date().getMonth() + 1;
      // 日
      var myday = new Date().getDate();
      // 星期
      var weekdayList = [
        "星期日",
        "星期一",
        "星期二",
        "星期三",
        "星期四",
        "星期五",
        "星期六",
      ];
      var myweekday = new Date().getDay();
      var weekday = weekdayList[myweekday];
      // 时 分
      var time = new Date();
      var hour = "00" + time.getHours();
      var minute = "00" + time.getMinutes();
      var attime =
        hour.substring(hour.length - 2, hour.length) +
        ":" +
        minute.substring(minute.length - 2, minute.length);

      var dataTime = mymonth + "月" + myday + "日 " + weekday + " " + attime;
      this.clockData = dataTime;
    },
    // 天气
    setWeather() {
      var ajaxUrl = "/sk/101210707.html";

      const ajaxData = {};
      ajaxData.weaid = "101210707";

      try {
        axios({
          // 请求方式
          method: "GET",
          // 请求的地址 不可为.json之类的本地路径
          url: ajaxUrl,
          // url携带的参数，适合get请求
          params: ajaxData,
          baseURL: "http://www.weather.com.cn/data",
        }).then(function (result) {
          console.log(result);

          const info = result.data.result;
          const weatherText =
            info.citynm +
            " " +
            info.weather +
            " " +
            info.temperature +
            " " +
            info.humidity +
            " " +
            info.wind;
          this.weatherData = weatherText;
        });
      } catch (error) {}
      // var info = data.weatherinfo;
      // { "weatherinfo": { "city": "乐清", "cityid": "101210707", "temp": "24.1", "WD": "西南风", "WS": "小于3级", "SD": "80%", "AP": "1007.3hPa", "njd": "暂无实况", "WSE": "<3", "time": "17:50", "sm": "2.9", "isRadar": "0", "Radar": "" } }
      // var weatherText = info.city + " 温度：" + info.temp + " 湿度：" + info.SD +" 湿度：" + info.SD +

      // {"result":{"weaid":"1194","days":"2019-10-09","week":"星期三","cityno":"leqing","citynm":"乐清","cityid":"101210707","temperature":"25℃/19℃","temperature_curr":"23℃","humidity":"69%","aqi":"-1","weather":"阴转多云","weather_curr":"阴","weather_icon":"http://api.k780.com/upload/weather/d/2.gif","weather_icon1":"","wind":"东北风","winp":"2级","temp_high":"25","temp_low":"19","temp_curr":"23","humi_high":"0","humi_low":"0","weatid":"3","weatid1":"","windid":"1","winpid":"2","weather_iconid":"2"}}
    },
    // 切换搜索
    switchSeach() {
      this.baiduSeach_displayStatus = !this.baiduSeach_displayStatus;
      this.googleSeach_displayStatus = !this.googleSeach_displayStatus;
    },
    // 获取图标地址
    getIconUrl(groupContent_item) {
      let styleVal = "";
      if (groupContent_item.hasOwnProperty("localIconUrl")) {
        if (groupContent_item.localIconUrl) {
          try {
            let iconData = require("@/" + groupContent_item.localIconUrl);
            styleVal = `background-image:url(${iconData})`;
          } catch (error) {}
        }
      } else if (groupContent_item.hasOwnProperty("remoteIconUrl")) {
        if (groupContent_item.remoteIconUrl) {
          styleVal = `background-image:url( ${groupContent_item.remoteIconUrl}) `;
        }
      }

      if (!styleVal) {
        let iconData = require("@/" +
          "assets/icon/navigation/favicon/internet.png");
        styleVal = `background-image:url(${iconData})`;
      }

      return styleVal;
    },
    // 跳转到书签页
    goBookmarkPage(item) {
      this.$store.commit("changeNavigationPage_selectedTag", item);
      this.$store.commit("changepageName", "bookmark");
      this.$router.push({ name: "bookmark" });
    },
  },
  created() {
    this.setDataTime();
    setInterval(this.setDataTime, 30000);
    this.setWeather();

    this.collectContentData = navData.navConetntData;
    this.bookmarksContentData = navData.navFooterData;

    const path = require("path");
    this.sever_url = path.join(__dirname);
    console.log("服务器地址：" + this.sever_url);
  },
};
</script>

<style lang="less" scoped>
.hide {
  display: none !important;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* text-align: center; */
  /* height: 1080px; */
  font-size: 16px;
}

#navigation_page {
  position: fixed;
  display: inline-block;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  overflow: auto;
}

#navigation_header {
  height: 30px;
  min-width: 550px;
  border-width: 1px;
  border-style: none none solid none;
  border-color: #ddd;
  position: fixed;
  display: block;
  width: 100%;
  top: 0;
  left: 0;
  background-color: #fff;
  .navigation_header_item {
    position: absolute;
    display: inline-block;
    overflow: hidden;
    border: 0px;
    font-size: 16px;
    width: 49%;
    line-height: 30px;
    color: #555;
  }

  #weather {
    left: 1%;
    text-align: left;
  }

  #clock {
    right: 1%;
    text-align: right;
  }
}

#content {
  position: relative;
  display: inline-block;
  width: 100%;
  text-align: center;
}

#navigation_search {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 200px;

  .seach_from {
    position: relative;
    width: 640px;
    height: 45px;
    margin: 0 auto;
  }

  #baiduSeach {
    display: inline-block;
  }

  .seach_logo {
    position: relative;
    display: inline-block;
    margin: 0 10px;
  }

  .seach_logo > i {
    position: relative;
    display: inline-block;
    height: 40px;
    width: 40px;
    background-position: 0 0;
    background-size: 100% 100%;
  }

  .seach_logo:hover {
    cursor: pointer;
  }

  #bd_logo > i {
    background-image: url(../../assets/icon/navigation/seach/bd_logo.png);
  }

  #google_logo > i {
    background-image: url(../../assets/icon/navigation/seach/google_logo.jpg);
  }

  .seach_input {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin-left: 10px;
    width: 450px;
    height: 40px;
    text-indent: 8px;
    font-size: 18px;
    font-family: Arial, Helvetica, sans-serif;
    border: 1px solid #d8d8d8;
    border-radius: 1px;
  }

  .seach_input:hover {
    border-color: #3388ff;
    box-shadow: 2px 2px 3px #ccc;
  }

  .seach_button {
    position: relative;
    display: inline-block;
    vertical-align: top;
    width: 102px;
    height: 44px;
    color: #ffffff;
    border-radius: 2px;
    cursor: pointer;
    border: none;
  }

  #b_btn {
    background-color: #3388ff;
  }

  #g_btn {
    background-color: #4285f4;
  }
}

#collect {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  #collect_content {
    position: relative;
    display: inline-block;
    margin: 0 auto 50px;
    width: 870px;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #e9e9e9;
  }

  #collect_content:hover {
    border-width: 0;
    box-shadow: 2px 2px 10px #ccc;
  }

  .nav-group {
    position: relative;
    display: block;
    margin: 0 auto;
    width: 860px;
    border-width: 0px;
    border-color: #ccc;
  }

  .nav-group-name {
    position: relative;
    display: inline-block;
    width: 80px;
    height: 100%;
    margin-left: 20px;
    border-width: 1px;
    border-style: none none dashed none;
    border-color: #dddddd;
    font-size: 16px;
    vertical-align: top;
  }

  .nav-group-name-text {
    position: relative;
    display: inline-block;
    width: 100%;
    line-height: 40px;
    height: 40px;
    text-align: left;
    font-size: 16px;
  }

  .nav-group-content {
    position: relative;
    display: inline-block;
    width: 750px;
    vertical-align: top;
  }

  .nav-group-unit {
    float: left;
    width: 150px;
    height: 40px;
    line-height: 40px;
    border-width: 1px;
    border-style: none none dashed none;
    border-color: #dddddd;
    font-size: 14px;
    border-radius: 2px;
  }

  .nav-group-unit:hover {
    background-color: #f1f1f1e0;
  }

  .nav-group-unit > a {
    position: relative;
    float: left;
    width: 100%;
    height: 100%;
  }

  .dir-item-last .dir-name {
    float: left;
    width: 80px;
    height: 30px;
    margin-top: 10px;
    margin-left: 20px;
    text-align: left;
    font-size: 16px;
    border-width: 1px;
    border-color: #dddddd;
    border-style: none;
  }

  .dir-item-last .nav-group-unit {
    float: left;
    width: 150px;
    height: 30px;
    padding-top: 10px;
    font-size: 16px;
    border-style: none;
  }

  .nav-icon {
    height: 16px;
    width: 16px;
    background-size: 100% 100%;
  }

  .nav-icon:hover {
    height: 20px;
    width: 20px;
  }

  .nav-icon-span {
    position: absolute;
    display: inline-block;
    line-height: 20px;
    text-align: center;
    width: 20px;
    z-index: -1;
    background-color: #ecebeb;
    height: 20px;
    border-radius: 3px;
    margin-top: 8px;
  }

  .del-icon {
    float: left;
    padding-left: 10px;
    padding-top: 3px;
  }

  .del-icon > i {
    display: inline-block;
    width: 16px;
    height: 16px;
    background-repeat: no-repeat;
  }

  .nav-text {
    float: left;
    padding-left: 5px;
    color: #333;
    vertical-align: top;
    width: 115px;
    text-align: left;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}

/* .bookmarks_css {
  width: 800px;
  height: 60px;
  padding: 15px 15px;
  border-width: 0px;
  border-style: solid;
  margin: 300px auto 0px;
} */

#bookmarks {
  position: relative;
  display: flex;
  width: 100%;
  border-width: 0px;
  border-style: solid;
  border-color: #03f;
  border-radius: 3px;
  opacity: 1;
  justify-content: center;

  #bookmarks_content {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    width: 890px;
    justify-content: center;
  }

  .bookmarks_haveContent {
    box-shadow: "2px 2px 10px #ededed";
  }

  .bookmarks_content_item {
    position: relative;
    display: inline-block;
    vertical-align: top;
    margin: 9px;
    border-width: 1px;
    border-style: dashed;
    border-color: #94dcfe;
    border-radius: 3px;
    text-align: center;
  }

  .bookmarks_content_item:hover {
    border-style: solid;
    border-color: #6bc1ff;
    background-color: #6bc1ff;
    box-shadow: 1px 1px 4px 1px #bdbdbd;
    cursor: pointer;
  }

  .bookmarks_content_item:active {
    position: relative;
    top: 2px;
  }

  .bookmarks_content_item > a {
    padding: 9px;
    text-decoration: none;
    /*a元素去除修饰文本*/
    color: #09f;
    border: 0px;
    font-size: 20px;
    line-height: 40px;
  }

  .bookmarks_content_item > a:hover {
    color: #fff;
  }

  .bookmarks_content_item[couldClick="no"] {
    border-color: #aeaeae;
    background-color: #ccc;
  }

  .bookmarks_content_item[couldClick="no"] > a {
    color: #ffffff;
  }

  .bookmarks_content_item[couldClick="no"] > a:hover {
    cursor: not-allowed;
  }
}

.arrow {
  animation: float 0.7s infinite alternate;
  transition: background 0.4s, border 0.2s, margin 0.2s;
}

@keyframes float {
  0% {
    margin-top: 0px;
  }
  100% {
    margin-top: 5px;
  }
}

#play {
  position: absolute;
  display: inline-block;
  top: 300px;
  left: 0;
  width: 30px;
  height: 50px;
  background-color: #509cff;
}

/* 侧边栏 */

#sidebar_event {
  position: fixed;
  display: inline-block;
  width: 30px;
  height: 100%;
  /* background-color: #f4f2f266; */
