<template>
  <div id="index_page">
    <textarea
      v-on:input="textarea_change"
      name=""
      ref="index_page_textarea_node"
      id="index_page_textarea"
      v-model="index_page_textarea"
    ></textarea>

     <router-view></router-view>
  </div>
</template>

<script>
export default {
  data() {
    return {
      index_page_textarea: "",
    };
  },
  methods: {
    textarea_change() {
      console.log(this.index_page_textarea);
      if (this.index_page_textarea == "1115") {
          this.goPcPage()
      }
    },
    goPcPage() {
      this.$router.push({ path: "@/components/navigation/navigation.vue" });
    },
  }, 
  created() {},
  mounted() {
        this.goPcPage()
    this.$refs.index_page_textarea_node.focus();
  },
};
</script>

<style lang="less">
#index_page {
  background-color: rgb(12, 12, 12);
  position: fixed;
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

#index_page_textarea {
  color: #fff;
  background-color: #0f0f0f00;
  border: none;
  position: absolute;
  display: inline-block;
  left: 1%;
  top: 1%;
  width: 98%;
  height: 98%;
  font-size: 18px;
}
</style> 